.nav {
  background: #FFF;
  padding: 10px 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;

  $root: &;

  @media(max-width: 991px) {
    padding: 10px 20px;
  }
  @media(max-width: 767px) {
    padding: 20px;
  }

  &__list {
    list-style: none;
    display: flex;
    align-items: center;
    @media(max-width: 767px) {
      display: none;
    }
  }

  &__item {
    margin-right: 37px;
    @media(max-width: 1199px) {
      margin-right: 30px;
    }
    @media(max-width: 991px) {
      margin-right: 20px;
    }
    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    color: $dark-color;
    font-weight: bold;
    font-size: 14px;
    line-height: 22px;
    text-transform: uppercase;
    letter-spacing: 0.15px;
    padding: 9px 0;
    transition: color .2s ease;

    @media(max-width: 1199px) {
      font-size: 13px;
      line-height: 19px;
    }

    &:hover, &:focus, &:active, &--active {
      color: $red-color;
      text-decoration: none;

      #{$root} {
        &__ico {
          color: $red-color;
        }
      }
    }
  }

  &__ico {
    width: 20px;
    height: 17px;
    position: relative;
    margin-top: -1px;
    color: $dark-color;
    margin-right: 5px;
    transition: color .3s ease-in-out;
  }

  &__mobile {
    display: none;
    @media(max-width: 767px) {
      display: flex;
    }
  }

  &__mobile-ico {
    width: 24px;
    height: 16px;
    fill: $dark-color;
  }

  &__messenger {
    width: 22px;
    height: 22px;
    display: inline-flex;
    align-items: center;
    justify-content: center;

    &:hover {
      #{$root} {
        &__messenger-ico {
          color: $whatsapp-color;
        }
      }
    }
  }

  &__messenger-ico {
    width: 100%;
    height: 100%;
    color: $dark-color;
  }
}