.appointment-small {
  &__content {
    background: #FFF;
    padding: 43px 76px 48px 70px;
    position: relative;
    margin-top: -78px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media(max-width: 767px) {
     flex-direction: column;
     padding: 30px;
    }
    @media(max-width: 575px) {
      padding: 30px 15px;
    }
  }
  &__title {
    font-size: 24px;
    font-weight: 900;
    line-height: 30px;
    width: 100%;
    max-width: 65%;
    margin-right: 15px;

    @media (max-width: 1199px) {
      font-size: 22px;
      line-height: 28px;
    }

    @media(max-width: 991px) {
      max-width: calc(100% - 226px);
    }

    @media(max-width: 767px) {
      font-size: 20px;
      line-height: 24px;
      max-width: 450px;
      text-align: center;
      margin-bottom: 15px;
    }

    @media(max-width: 575px) {
      font-size: 18px;
    }

    @media(max-width: 359px) {
      font-size: 16px;
      line-height: 21px;
    }
  }
}